.sign-statistics {
  background: #2a273b;
  width: 100%;
  padding: 19px;
  display: flex;
}
.sign-statistics .sign-statistics-left {
  position: relative;
  height: calc(100vh - 138px);
  padding-left: 51px;
  flex: 1;
}
.sign-statistics .sign-statistics-left .ant-tabs-tab {
  color: #fff;
}
.sign-statistics .sign-statistics-left > p {
  font-size: 16px;
  color: #fff;
  line-height: 16px;
  border-left: 2px solid #fff;
  padding-left: 8px;
}
.sign-statistics .sign-statistics-left .sign-statistics-list {
  height: calc(100% - 184px);
}
.sign-statistics .sign-statistics-left .sign-statistics-list .ant-col {
  width: 135px;
  height: 135px;
  background: #302e45;
  box-shadow: 0px 2px 10px 0px #232031;
  margin: 20px 35px 20px 0;
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
}
.sign-statistics .sign-statistics-left .sign-statistics-list .ant-col img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 2px solid rgba(255, 255, 255, 0.5);
  margin: 29px 0 9px;
}
.sign-statistics .sign-statistics-right {
  color: rgba(255, 255, 255, 0.8);
  height: calc(100vh - 138px);
}
.sign-statistics .sign-statistics-right .ant-row {
  margin: 18px 0;
}
.sign-statistics .sign-statistics-right .ant-row img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
.sign-statistics .sign-statistics-right > div {
  background: #302e45;
  width: 280px;
  padding: 0 20px;
}
.sign-statistics .sign-statistics-right > div h3 {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 10px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.sign-statistics .sign-statistics-right > div h3 span {
  margin: 0 5px;
}
.sign-statistics .sign-statistics-right > div:nth-of-type(1) {
  min-height: 36%;
  height: 36%;
  margin-bottom: 20px;
  text-align: center;
}
.sign-statistics .sign-statistics-right > div:nth-of-type(1) img {
  margin-bottom: 10px;
}
.sign-statistics .sign-statistics-right > div:nth-of-type(2) {
  height: calc(64% - 20px);
}
.sign-statistics .sign-statistics-right > div:nth-of-type(2) > div {
  height: calc(100% - 45px);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.sign-statistics .sign-statistics-right > div:nth-of-type(2) p {
  text-align: center;
}
.sign-statistics .sign-statistics-right .ant-progress-inner {
  width: 150px !important;
  height: 150px !important;
}
.sign-statistics .sign-statistics-right .ant-progress-text {
  color: #fff;
}
.sign-statistics .sign-statistics-left .change-sign-list {
  min-width: 771px;
  overflow: auto;
}
.sign-statistics .sign-statistics-left .change-sign-list .ant-col {
  width: 150px;
  height: 150px;
  margin: 20px 21px;
}
.sign-statistics .ant-tabs {
  padding-left: 21px;
  padding-right: 77px;
}
.sign-statistics .sign-statistics-left .change-sign-list::-webkit-scrollbar {
  display: none;
}
.sign-statistics .ant-tabs-nav .ant-tabs-tab-active {
  border-bottom: 2px solid #FFFFFF;
}
.sign-statistics .ant-tabs-bar {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2) !important;
}
.ant-progress-inner {
  height: auto !important;
}
.sign-statictics-top {
  width: 100%;
  height: calc(100% - 53px);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.sign-statictics-top p {
  margin-bottom: 0px;
}
